<template>
  <el-pagination
    background
    layout="prev, pager, next"
    @current-change="change_page"
    :page-count="$store.state.page_total"
    :hide-on-single-page="true"
  >
  </el-pagination>
  <div class="copyright">Copyright 2021 Zhang Yang</div>
</template>
<script>
export default {
  methods: {
    get_page_total() {
      if (!(this.$route.path === "/" || this.$route.path === "/links")) {
        this.change_page_total(1);
      }
    },
    change_page(current_page) {
      this.$store.commit("update_page_now", current_page);
    },
    change_page_total(total) {
      this.$store.commit("update_page_total", total);
    },
  },
  created() {
    this.get_page_total();
  },
  watch: {
    $route() {
      this.get_page_total();
    },
  },
};
</script>
<style lang="scss" scoped>
.copyright {
  margin-top: 12px;
  opacity: 0.6;
}
</style>